//animation by class init
const workScaleBig = document.querySelectorAll('.ScaleBigJs');
const workScaleMini = document.querySelectorAll('.ScaleMiniJs');

workScaleBig.forEach(item => {
  const workImgScroll = new Waypoint({
    element: item,
    handler: function() {
      const taskTranslation = anime({
        targets: item,
        scale: [1.5, 1],
        easing: 'linear',
        duration: 500,
      });
      this.destroy()
    },
    offset: '100%',
  })
})

workScaleMini.forEach(item => {
  const workImgScroll = new Waypoint({
    element: item,
    handler: function() {
      const taskTranslation = anime({
        targets: item,
        scale: [0.7, 1],
        easing: 'linear',
        duration: 500,
      });
      this.destroy()
    },
    offset: '100%',
  })
})

//home page animation
const mainTitle1 = document.querySelector('.home-main-title__name--first');
const mainTitle2 = document.querySelector('.home-main-title__name--second');
const logo = document.querySelector('.logo');
const mainLine = document.querySelector('.home-main-line');

window.onload = () => {
  const mainTitle1Anim = anime({
    targets: mainTitle1,
    translateX: ['-150%', '-50%'],
    opacity: ['0', '1'],
    autoplay: true,
    delay: 1200,
    easing: 'linear',
    duration: 200
  });

  const mainTitle2Anim = anime({
    targets: mainTitle2,
    translateX: ['150%', '-50%'],
    opacity: ['0', '1'],
    autoplay: true,
    delay: 1200,
    easing: 'linear',
    duration: 500
  });

  const logoAnim = anime({
    targets: logo,
    opacity: ['0', '1'],
    autoplay: true,
    delay: 1500,
    easing: 'linear',
    duration: 800
  });

  const mainLineAnim = anime({
    targets: mainLine,
    translateX: '-50%',
    translateY: '-50%',
    scale: [0, 1],
    autoplay: true,
    delay: 1900,
    easing: 'linear',
    duration: 500
  });
}

//about img animation
const aboutImg = document.querySelector('.about-main__img-wrapper');
const aboutArticle = document.querySelector('.about-article');

if(aboutImg) {
  window.onload = () => {
    const aboutImgAnim = anime({
      targets: aboutImg,
      translateX: ['-150%', '0%'],
      autoplay: true,
      delay: 2000,
      easing: 'spring(1, 50, 10, 0)',
      duration: 500,
    });

    const aboutArticleAnim = anime({
      targets: aboutArticle,
      translateX: ['150%', '0%'],
      autoplay: true,
      delay: 2000,
      easing: 'spring(1, 50, 10, 0)',
      duration: 500,
    });
  }
}

//works links animation
let worksLinkAnim;

if(window.matchMedia("(min-width: 992px)").matches) {
  worksLinkAnim = anime({
    targets: '.works__link',
    translateY: ['150%', '0%'],
    easing: 'linear',
    duration: 600,
    delay: anime.stagger(200, {start: 2500}),
  });
} else {
  worksLinkAnim = anime({
    targets: '.works__link',
    translateX: ['-400%', '0%'],
    easing: 'linear',
    duration: 600,
    delay: anime.stagger(200, {start: 2500}),
  });
}


//article prev, tasks and list animation
const articlePrevTasks = document.querySelectorAll('.article-preview__task');
const articlePrevTitleMini = document.querySelectorAll('.article-preview__title-mini');

articlePrevTasks.forEach(task => {
  const waypoint = new Waypoint({
    element: task,
    handler: function() {
      const taskTranslation = anime({
        targets: task,
        opacity: ['0', '1'],
        translateY: ['50%', '0%'],
        easing: 'linear',
        duration: 500
      });
    },
    offset: '100%',
  })
})

articlePrevTitleMini.forEach(task => {
  const waypoint = new Waypoint({
    element: task,
    handler: function() {
      const titleMiniTranslation = anime({
        targets: task,
        opacity: ['0', '1'],
        translateY: ['-50%', '0%'],
        easing: 'linear',
        duration: 500
      });
    },
    offset: '100%',
  })
})

const previewList  = document.querySelectorAll('.article-preview__list');
const articlePreview = document.querySelector('.article-preview');
const workMain = document.querySelector('.work-main');
let previewItem;
let listAnim;

previewList.forEach(list => {
  const listScroll = new Waypoint({
    element: list,
    handler: function() {
      if(articlePreview) {
        previewItem = document.querySelectorAll('.article-preview .article-preview__item');
        listAnim = anime({
          targets: previewItem,
          opacity: ['0', '1'],
          easing: 'linear',
          duration: 200,
          delay: anime.stagger(200),
        });
      } else if(workMain) {
        previewItem = document.querySelectorAll('.work-main .article-preview__item');
        listAnim = anime({
          targets: previewItem,
          opacity: ['0', '1'],
          easing: 'linear',
          duration: 200,
          delay: anime.stagger(200, {start: 3500}),
        });
      }
    },
    offset: '100%',
  })
})

//work animation

const workMainTitle = document.querySelector('.work-main__title');
const workMainLink = document.querySelector('.work-main__link-back');
const workMainText = document.querySelector('.work-main__text');
const workTextS = document.querySelectorAll('.work-text');

const workMainTl = anime.timeline({
  easing: 'easeOutExpo',
  duration: 300,
});

workMainTl
.add({
  targets: workMainTitle,
  delay: 2500,
  opacity: ['0', '1'],
})
.add({
  targets: workMainLink,
  translateX: ['-300%', '0%'],
})
.add({
  targets: workMainText,
  translateX: ['300%', '0%'],
});

workTextS.forEach(text => {
  const workTextScroll = new Waypoint({
    element: text,
    handler: function() {
      const textAnim = anime({
        targets: text,
        opacity: ['0', '1'],
        easing: 'linear',
        duration: 500,
      });
    },
    offset: '100%',
  })
})

//contact animation

const contactLinesAnim = anime({
  targets: '.contact__line',
  opacity: ['0', '1'],
  translateY: ['50%', '0%'],
  easing: 'linear',
  duration: 600,
  delay: anime.stagger(300, {start: 1500}),
});

//development animation

