//HomePage CenterContainer
const centerContainer = document.querySelector('.home-main-center');
const centerImg = document.querySelector('.home-main-center__img');
const centerVideo = document.querySelector('.home-main-center__video');

const showVideo = (el) => {
  el.style.display = 'block';
  el.style.zIndex = '2';
}

const hideVideo = (el) => {
  el.currentTime = 0;
  el.style.display = 'none';
  el.style.zIndex = '-1';
}


if(centerContainer) {
  centerContainer.addEventListener('mouseover', (e) => {
    e.preventDefault();
    showVideo(centerVideo);
  });
  centerContainer.addEventListener('click', (e) => {
    e.preventDefault();
    showVideo(centerVideo);
  });
}

if(centerVideo) {
  centerVideo.onended = () => {
    hideVideo(centerVideo);
  };
}